import { useState } from "react";
import parse from "html-react-parser";
import { Button } from "@mui/material";

const EngagePlan = ({
  planStatus,
  selectedPlan,
  changePlan,
  currentPlan,
  name,
  sub_title,
  icon,
  imgAlt,
  currency,
  plan_type,
  plan_detail,
  // symbol,
  amount,
  rate,
  recommended,
  features,
  isMobile,
}) => {
  const [showAll, setShowAll] = useState(false);

  const planDetail = plan_detail && plan_detail.split("\n");

  const formatAmount = (amount) =>
    amount.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

  return (
    <div
      className={`plan-box${recommended ? " recommended" : ""}${
        selectedPlan === currentPlan ? " selected" : ""
      }`}
    >
      <h4 className="title">{name}</h4>
      <h5 className="subtitle">{sub_title}</h5>
      <div className="icon">{icon && parse(icon)}</div>
      <p className="price">
        <sup className="symbol">{currency === "USD" ? "$" : "₹"}</sup>
        <span className="value">{formatAmount(amount)}</span>
        <sub className="rate">{plan_type === "Monthly" ? "/mo" : ""}</sub>
      </p>
      <div className="additional">
        <a
          href="https://developers.facebook.com/docs/whatsapp/pricing"
          className="charges"
          target="_blank"
          rel="noreferrer nofollow"
        >
          {"WhatsApp Charges "}
          <svg
            width="13"
            height="13"
            viewBox="0 0 13 13"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M9.50563 9.03938V11.0197C9.50563 11.1248 9.4639 11.2255 9.38963 11.2998C9.31535 11.3741 9.21461 11.4158 9.10956 11.4158H1.98034C1.8753 11.4158 1.77456 11.3741 1.70028 11.2998C1.626 11.2255 1.58427 11.1248 1.58427 11.0197V3.8905C1.58427 3.78545 1.626 3.68471 1.70028 3.61043C1.77456 3.53616 1.8753 3.49443 1.98034 3.49443H3.96068C4.17077 3.49443 4.37225 3.41097 4.52081 3.26242C4.66936 3.11386 4.75282 2.91238 4.75282 2.70229C4.75282 2.4922 4.66936 2.29072 4.52081 2.14217C4.37225 1.99361 4.17077 1.91016 3.96068 1.91016H1.1882C0.873073 1.91016 0.570848 2.03534 0.348017 2.25817C0.125185 2.481 0 2.78323 0 3.09836L0 11.8119C0 12.127 0.125185 12.4292 0.348017 12.652C0.570848 12.8749 0.873073 13.0001 1.1882 13.0001H9.9017C10.2168 13.0001 10.5191 12.8749 10.7419 12.652C10.9647 12.4292 11.0899 12.127 11.0899 11.8119V9.03938C11.0899 8.82929 11.0064 8.62781 10.8579 8.47926C10.7093 8.3307 10.5079 8.24724 10.2978 8.24724C10.0877 8.24724 9.8862 8.3307 9.73764 8.47926C9.58909 8.62781 9.50563 8.82929 9.50563 9.03938Z"
              fill="#12B0EE"
            />
            <path
              d="M11.8821 0.325928H7.12929C6.69181 0.325928 6.33716 0.680579 6.33716 1.11806C6.33716 1.55555 6.69181 1.9102 7.12929 1.9102H11.8821C12.3196 1.9102 12.6742 1.55555 12.6742 1.11806C12.6742 0.680579 12.3196 0.325928 11.8821 0.325928Z"
              fill="#12B0EE"
            />
            <path
              d="M12.6741 5.87088V1.11806C12.6741 0.680579 12.3194 0.325928 11.8819 0.325928C11.4445 0.325928 11.0898 0.680579 11.0898 1.11806V5.87088C11.0898 6.30837 11.4445 6.66302 11.8819 6.66302C12.3194 6.66302 12.6741 6.30837 12.6741 5.87088Z"
              fill="#12B0EE"
            />
            <path
              d="M11.3317 0.556232L3.39922 8.48872C3.09204 8.7959 3.09198 9.29388 3.39908 9.60099C3.70619 9.90809 4.20417 9.90803 4.51135 9.60085L12.4438 1.66836C12.751 1.36118 12.7511 0.863196 12.444 0.556091C12.1369 0.248985 11.6389 0.249049 11.3317 0.556232Z"
              fill="#12B0EE"
            />
          </svg>
        </a>
      </div>
      <div className="action">
        <Button
          variant="contained"
          type="button"
          onClick={() =>
            changePlan(selectedPlan === currentPlan ? null : currentPlan)
          }
        >
          {selectedPlan === currentPlan ? "Selected " : "Select"}
          {selectedPlan === currentPlan && (
            <svg
              width="13"
              height="10"
              viewBox="0 0 13 10"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M12.047 2.19413L4.59491 9.15283L0 4.86212L2.04695 2.66787L4.59491 5.04713L10 -0.00012207L12.047 2.19413Z"
                fill="#ffffff"
              />
            </svg>
          )}
        </Button>
      </div>
      <div className="ruler">
        <hr />
      </div>
      <ul
        className={`features${
          isMobile ? (showAll ? " show-all" : " show-less") : ""
        }`}
      >
        {/*         {features.map((feature, index) => (
          <li key={index} className={feature.includes("*") ? "star" : ""}>
            {feature.includes("*") ? feature.slice(1) : feature}
          </li>
        ))} */}
        {/* {planDetail.map((feature, index) => (
          <li key={index} className={feature.includes("*") ? "star" : ""}>
            {feature.includes("*") ? feature.slice(1) : feature}
          </li>
        ))} */}
        {parse(plan_detail)}
      </ul>
      {isMobile ? (
        <div className={`expand${showAll ? " up" : " down"}`}>
          <button type="button" onClick={() => setShowAll(!showAll)}>
            {showAll ? "SHOW LESS FEATURES" : "SEE ALL FEATURES"}
          </button>
        </div>
      ) : (
        <div className="action">
          <Button
            variant="contained"
            type="button"
            onClick={() =>
              changePlan(selectedPlan === currentPlan ? null : currentPlan)
            }
          >
            {selectedPlan === currentPlan ? "Selected " : "Select"}
            {selectedPlan === currentPlan && (
              <svg
                width="13"
                height="10"
                viewBox="0 0 13 10"
                fill="none"
                xmlns="//www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M12.047 2.19413L4.59491 9.15283L0 4.86212L2.04695 2.66787L4.59491 5.04713L10 -0.00012207L12.047 2.19413Z"
                  fill="#ffffff"
                />
              </svg>
            )}
          </Button>
        </div>
      )}
    </div>
  );
};

export default EngagePlan;
