/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-lone-blocks */
import { Fragment, useEffect, useMemo, useState } from "react";
import { useHistory } from "react-router-dom";
import { Helmet } from "react-helmet";
import { Hidden, useMediaQuery } from "@material-ui/core";
import { useTheme } from "@material-ui/core/styles";

import UIButton from "../../Components/UI/UIButton/UIButton";
import Header from "../../Components/Header/SignUpSetup";

import useStyles from "./styles/muiStyles";
import EngagePlan from "./plan/engage";
import CuratePlan from "./plan/curate";

import EngageImg from "./assets/engage.svg";
import CurateImg from "./assets/curate.svg";
import SecureImg from "./assets/secure.svg";
import BasicImg from "./assets/basic.svg";
import SmartImg from "./assets/smart.svg";
import ProfessionalImg from "./assets/professional.svg";
import FreeImg from "./assets/free.svg";
import ProImg from "./assets/pro.svg";

import { useToastMessage } from "../../contexts";
import { getPlans } from "../../api/Plan";

const ChoosePlan = () => {
  const history = useHistory();
  const toastMessage = useToastMessage();
  const classes = useStyles(),
    theme = useTheme(),
    isMobile = useMediaQuery(theme.breakpoints.down("sm")),
    [check, setCheck] = useState(false),
    [selectedProduct, changeProduct] = useState("Engage"),
    [selectedCuratePlan, changeCuratePlan] = useState(null),
    [selectedEngagePlan, changeEngagePlan] = useState(null),
    [checkoutPlan, setCheckoutPlan] = useState({
      symbol: "₹",
      totalPrice: 0,
      slug: [],
    }),
    [engagePlans, setEngagePlans] = useState([]),
    [curatePlans, setCuratePlans] = useState([]),
    //[choosedPlanData, setChoosedPlanData] = useState({}),

    switchProduct = (product) => {
      const App = document.querySelector("#root .App"),
        productBLock = document.querySelector(`.${product.toLowerCase()}`);
      changeProduct(product);
      App.style.scrollBehavior = "smooth";
      App.scrollTop = productBLock.offsetTop;
      setTimeout(() => {
        App.removeAttribute("style");
      }, 500);
    };

  useEffect(() => {
    getPlanList();
  }, []);

  const getPlanList = async () => {
    const res = await getPlans();
    console.log("res", res);
    if (!res.success) {
      return toastMessage.setErrorMessage(res.message);
    } else {
      setEngagePlans(res.payload.filter((plan) => plan.category === "engage"));
      setCuratePlans(res.payload.filter((plan) => plan.category === "curate"));
    }
  };

  useEffect(() => {
    let selectPlan = [];
    let selectedPlanDetail = [];
    let amount = 0;
    let symbol;
    {
      if (selectedEngagePlan !== "" && selectedEngagePlan !== null) {
        selectPlan.push(selectedEngagePlan);
        let engage = engagePlans.filter(
          (plan) => plan.slug === selectedEngagePlan
        );
        /*         selectedPlanDetail.push({
          slug: selectedEngagePlan,
          amount: engage[0]["amount"],
        }); */
        selectedPlanDetail.push(engage[0]);
        if (engage[0]["amount"] !== "FREE") {
          amount = engage[0]["amount"];
        }
        symbol = engage[0]["currency"] === "USD" ? "$" : "₹";
      }
      if (selectedCuratePlan !== "" && selectedCuratePlan !== null) {
        let curate = curatePlans.filter(
          (plan) => plan.slug === selectedCuratePlan
        );
        /*         selectedPlanDetail.push({
          slug: selectedCuratePlan,
          amount: curate[0]["amount"],
        }); */

        selectedPlanDetail.push(curate[0]);

        if (curate[0]["amount"] !== "FREE") {
          amount += curate[0]["amount"];
        }
        selectPlan.push(selectedCuratePlan);
        symbol = curate[0]["currency"] === "USD" ? "$" : "₹";
      }
      if (selectedPlanDetail.length > 0) {
        localStorage.setItem(
          "selectedPlanDetail",
          JSON.stringify(selectedPlanDetail)
        );
      }
      setCheckoutPlan({
        ...checkoutPlan,
        slug: selectPlan,
        totalPrice: amount,
        symbol: symbol,
      });
    }
  }, [selectedEngagePlan, selectedCuratePlan]);

  const savePlanInPreBusiness = async () => {
    console.log("submit", checkoutPlan);
    const App = document.querySelector("#root .App");
    App.scrollTop = 0;
    history.push({
      pathname: `/business-setup`,
      state: { from: "choose-plan" },
    });
  };

  const formatAmount = (amount) =>
    amount.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>Choose Plan - EasySocial.io</title>
        <link rel="canonical" href="https://easysocial.io/" />
        <meta
          name="keywords"
          content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
        />
        <meta
          name="description"
          content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
        />
      </Helmet>
      <div className={classes["choose-plan"]}>
        <Header />
        <section className={classes.selection}>
          <div className="content">
            <Hidden mdDown implementation="js">
              <div className="block-left">
                <h4>{"Choose a Product"}</h4>
                <button
                  type="button"
                  className={`btn-plan${
                    selectedProduct === "engage" ? " active" : ""
                  }`}
                  onClick={() => switchProduct("engage")}
                >
                  <span className="icon">
                    <img src={EngageImg} alt="engage" />
                    {selectedProduct === "engage" && (
                      <svg
                        width="13"
                        height="11"
                        viewBox="0 0 13 11"
                        fill="none"
                        xmlns="//www.w3.org/2000/svg"
                      >
                        <path
                          fillRule="evenodd"
                          clipRule="evenodd"
                          d="M12.047 3.19035L4.59491 10.149L0 5.85834L2.04695 3.66408L4.59491 6.04335L10 0.996094L12.047 3.19035Z"
                          fill="#32C5FF"
                        />
                      </svg>
                    )}
                  </span>
                  <span className="label">
                    {"Manage & engage in conversations with WhatsApp chat bot."}
                  </span>
                </button>
                {/* <button
                  type="button"
                  className={`btn-plan${
                    selectedProduct === "Curate" ? " active" : ""
                  }`}
                  onClick={() => switchProduct("Curate")}
                >
                  <span className="icon">
                    <img src={CurateImg} alt="Curate" />
                    {selectedProduct === "Curate" && (
                      <svg
                        width="13"
                        height="11"
                        viewBox="0 0 13 11"
                        fill="none"
                        xmlns="//www.w3.org/2000/svg"
                      >
                        <path
                          fillRule="evenodd"
                          clipRule="evenodd"
                          d="M12.047 3.19035L4.59491 10.149L0 5.85834L2.04695 3.66408L4.59491 6.04335L10 0.996094L12.047 3.19035Z"
                          fill="#FF53B0"
                        />
                      </svg>
                    )}
                  </span>
                  <span className="label">
                    {
                      "All social media in one place - Instagram, Facebook & more."
                    }
                  </span>
                </button> */}
              </div>
            </Hidden>
            <div className="block-right">
              <div className="engage">
                <div className="plans-header">
                  <span className="badge">{"ENGAGE"}</span>
                  <h2 className="title">{"WhatsApp ChatBot"}</h2>
                  {/*
                    <div className="plan-changer">
                      <span className={!check ? "label active" : "label"}>
                        {"MONTHLY"}
                      </span>
                      <div className="switch">
                        <input
                          type="checkbox"
                          className="control"
                          id="change-plan"
                          defaultChecked={check}
                        />
                        <label
                          aria-label="Change Plan"
                          className="view"
                          htmlFor="change-plan"
                          onClick={() => setCheck(!check)}
                        />
                      </div>
                      <span className={check ? "label active" : "label"}>
                        {"ANNUAL"}
                      </span>
                    </div>
                   */}
                  {/*
                    <p className="statement">
                      {"Save "}
                      <em>{"upto 20%"}</em>
                      {" on annual plans!"}
                    </p>
                   */}
                  {false && <br />}
                  {false && <br />}
                  <div className="offer-block">
                    <div className="icon">
                      <img src={SecureImg} alt="secure" />
                    </div>
                    <div className="info">
                      <h5>{"MONEY BACK GUARANTEE"}</h5>
                      <p>
                        {`Your first 200 conversations are on us. If you don’t like us, you get `}
                        <strong>{"Full Refund."}</strong>
                      </p>
                    </div>
                  </div>
                </div>
                <div className="plans-lists justify-content-center">
                  {engagePlans.map((plan, index) => {
                    if (!check && plan.plan_type === "monthly") {
                      return (
                        <EngagePlan
                          key={index}
                          currentPlan={plan.slug}
                          selectedPlan={selectedEngagePlan}
                          changePlan={changeEngagePlan}
                          isMobile={isMobile}
                          {...plan}
                        />
                      );
                    }
                    if (check && plan.plan_type === "annual") {
                      return (
                        <EngagePlan
                          key={index}
                          currentPlan={plan.slug}
                          selectedPlan={selectedEngagePlan}
                          changePlan={changeEngagePlan}
                          isMobile={isMobile}
                          {...plan}
                        />
                      );
                    }
                  })}
                </div>
              </div>
              {/* <div className="curate">
                <div className="plans-header">
                  <span className="badge">{"CURATE"}</span>
                  <h2 className="title">{"Social Media Scheduling"}</h2>
                </div>
                <div className="plans-lists">
                  {curatePlans.map((plan, index) => {
                    if (!check && plan.plan_type === "monthly") {
                      return (
                        <CuratePlan
                          key={index}
                          currentPlan={plan.slug}
                          selectedPlan={selectedCuratePlan}
                          changePlan={changeCuratePlan}
                          isMobile={isMobile}
                          {...plan}
                        />
                      );
                    }
                    if (check && plan.plan_type === "Annual") {
                      return (
                        <CuratePlan
                          key={index}
                          currentPlan={plan.slug}
                          selectedPlan={selectedCuratePlan}
                          changePlan={changeCuratePlan}
                          isMobile={isMobile}
                          {...plan}
                        />
                      );
                    }
                  })}
                </div>
              </div> */}
            </div>
          </div>
        </section>
        {checkoutPlan && (
          <section className={classes["checkout-bar"]}>
            <div className="content">
              <div className="info">
                <h4 className="selection" style={{ maxWidth: "270px" }}>
                  {checkoutPlan.slug.length > 0
                    ? checkoutPlan.slug.length
                    : "No"}
                  {checkoutPlan.slug.length > 1 ? " Products " : " Product "}
                  {"Selected"}
                </h4>
                <p className="price">
                  <sup className="symbol">
                    {checkoutPlan.symbol ? checkoutPlan.symbol : "₹"}
                  </sup>
                  <span className="value">
                    {formatAmount(checkoutPlan.totalPrice)}
                  </span>
                  {/* <sub className="rate">{checkoutPlan.rate}</sub> */}
                </p>
              </div>
              <div className="action">
                {!isMobile && (
                  <a href="/pricing" target="_blank" className="learn-more">
                    {"Explore more products"}
                  </a>
                )}
                <UIButton
                  onClick={(e) => {
                    savePlanInPreBusiness();
                  }}
                >
                  {"Checkout"}
                </UIButton>
              </div>
            </div>
          </section>
        )}
      </div>
    </Fragment>
  );
};

export default ChoosePlan;
